.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1{
  color: #FFFFFF;
  font-family: 'Bungee Hairline', cursive;
  font-size: 60px;
  text-align: center;
}

h2{
  color: #FFFFFF;
  font-family: 'Bungee Hairline', cursive;
  font-size: 45px;
  text-align: center;
}

h3{
  color: #FFFFFF;
  font-family: 'Bungee Hairline', cursive;
  font-size: 25px;
  text-align: center;
  margin-top: 0px;
}

a{
  text-decoration: none;
}

.date{
  color: #FFFFFF;
  font-family: 'Bungee Hairline', cursive;
  font-size: 25px;
  text-align: center;
  text-decoration: underline;
  margin-top: 0px;
}

h4{
  color: #FFFFFF;
  font-family: 'Bungee Hairline', cursive;
  font-size: 30px;
  text-align: center;
  margin-bottom: 10px;
}

p1{
  color: #FFFFFF;
  font-family: 'Bungee Hairline', cursive;
  font-size: 18px;
}

p2{
  color: #FFFFFF;
  font-family: 'Inconsolata', monospace;
  font-size: 20px;
}

p3{
  color: #b1d0d8;
}

p4{
  color: #b1d0d8;
  background-color: #282828;
}

.social{
  color: #FFFFFF;
  font-family: 'Inconsolata', monospace;
  font-size: 20px;
  text-decoration: none;
}

#pswd{
  color: #94cc78;
}

.logo{
  text-decoration: none;
}

#menu{
  color: #FFFFFF;
  font-family: 'Bungee Hairline', cursive;
  font-size: 25px;
}

#list{
  display: inline-block;
  text-align: center;
  text-indent: -35px;
}

ul li{
  list-style-type: none;
}

nav{
  background-color: rgba(0,0,0,0.6);
  margin: 0px;
  overflow: hidden;
}

.wrapper {
  width:100%;
  position:sticky;
  top: 0px;
  z-index:10;
  background:inherit;
}

.content{
  display: contents;
  
  text-decoration: none;
}

nav ul{
  margin: 0;
  padding: 0;
  text-align: center;
}

nav ul li{
  display: inline-block;
  list-style-type: none;
}

nav ul li:hover{
  background-color: rgba(0,0,0,0.75);
}

nav > ul > li > a {
  display: block;
  line-height: 30px;
  padding: 5px 30px;
  text-decoration: none;
}

.box{
  align-content: center;
  text-align: center;
  background-color: rgba(0,0,0,0.6);
  padding: 5% 5%;
  margin: 2.5% 10%;
}

img{
  width: 100%;
  margin: 0px 0px;
  object-fit: contain;
}

.socials{
  margin-right: 30px;
}

.writeup{
  width: 100%;
}

.blog{
  align-content: center;
  text-align: center;
  background-color: rgba(0,0,0,0.6);
  padding: 5% 5%;
  margin: 2.5% 10%;
}

#profile{
  border-radius: 500px;
  width: 200px;
  height: 200px;
}

.button{
  text-align: center;
  align-content: center;
  padding: 10px 10px;
  margin: 0px 30%;
  vertical-align: middle;
  background-color: rgba(0,0,0,0.6);
}

.button:hover{
  background-color: rgba(0,0,0,0.75);
}

.header{
  padding: 10px 10px;
}

.footer{
  background-color: rgba(0,0,0,0.6);
  padding: 10px 20px;
  margin: 20px 0px;
  text-align: center;
}

body{
  background-color: #000000;
  background-attachment: fixed;
  background-image: url(background.jpg);
}